var forge;
require.ensure([], function(require) {
    forge = require('../forge/lib/index.wn');
}, 'forge');

/**
 *  WN   Abstract .    .
 *    from~  parsing , to~   .
 * 
 *  (WNCertificate )
 * <pre>
 * WNCertificate cert = new WNCertificate(); //  .
 * 
 * from~.
 * cert.fromArrayBuffer(arrayBuffer); // ArrayBuffer .
 * cert.fromBase64(base64String); // Base64 .
 * cert.fromPem(pemString); // PEM . ( .)
 * 
 * to~
 * cert.toUint8Array(); // Uint8Array .
 * cert.toBase64(); // Base64 .
 * cert.toPem(); // PEM . ( .)
 * </pre>
 * @classdesc    Super
 * @class
 * @alias WNBase
 */
function WNBase() {
    // forge       ASN.1    .
    function toAsn1(obj) {
        if(obj._fn.asn1) {
            return obj._fn.asn1.to(obj._fo);
        } else {
            return obj._fo;
        }
    }
    
    /**
     *  Uint8Array  .
     * @function
     * @return {Uint8Array} Uint8Array 
     */
    this.toUint8Array = function() {
        var asn1 = toAsn1(this);
        var der = forge.asn1.toDer(asn1);
        return forge.util.binary.raw.decode(der.data);
    };

    /**
     *  Hexadecimal string  .
     * @function
     * @return {string} Hexadecimal string
     */
    this.toHex = function() {
        var asn1 = toAsn1(this);
        return forge.asn1.toDer(asn1).toHex();
    };

    /**
     *  Base64 string  .
     * @function
     * @return {string} Base64 string
     */
    this.toBase64 = function() {
        var asn1 = toAsn1(this);
        var der = forge.asn1.toDer(asn1);
        return forge.util.encode64(der.getBytes());
    };

    /**
     *  PEM  .
     * @function
     * @return {string} PEM string.     undefined.
     */
    this.toPem = function() {
        if(this._fn.pem && this._fn.pem.to) {
            return this._fn.pem.to(this._fo);
        }
    };

    /**
     * ArrayBuffer  .
     * 
     * @param {ArrayBuffer} buffer    ArrayBuffer. Uint8Array  .
     * @param {*} [o] _fn.asn1.from    parameter.  PKCS12 Password  .
     * 
     * @return {Object} this
     */
    this.fromArrayBuffer = function (buffer, o) {
        try {
            buffer = forge.util.createBuffer(buffer);
            var asn1 = forge.asn1.fromDer(buffer);
            if(this._fn.asn1)
                this._fo = this._fn.asn1.from(asn1, o);
            else
                this._fo = asn1; //     ASN.1  .
            return this;
        } catch(e) {
            var errId = (e.message && e.message.indexOf("password") > 0) ? "INVALID_PASSWORD" : "WRONG_DATA";
            throw { code: errId, message: KICA_ERROR_MSG.get(errId), error: new Error()};
        }
    }

    /**
     * Hexdecimal string  .
     * 
     * @param {string} hex    Hexadecimal.
     * 
     * @return {Object} this
     */
    this.fromHex = function(hex) {
        var buffer = forge.util.binary.hex.decode(hex);
        this.fromArrayBuffer(buffer);
        return this;
    };

    /**
     * Base64 string  .
     * 
     * @param {string} base64    Base64.
     * 
     * @return {Object} this
     */
    this.fromBase64 = function (base64) {
        var buffer = forge.util.binary.base64.decode(base64);
        this.fromArrayBuffer(buffer);
        return this;
    };


    /**
     * PEM string  .
     * 
     * @param {string} pem    PEM.
     * 
     * @return {Object} this
     */
    this.fromPem = function(pem) {
        this._fo = this._fn.pem.from(pem);
        return this;
    };
}

module.exports = WNBase;
